<template>
    <div class="cart-product-card">
        <div class="image">
            <img :src="product.image">
        </div>
        <div class="card-content">
            <h3 class="card-title">
                <router-link :to="{ name: 'product', params: { categoryId: product.category, productId: product.code } }">
                    {{ product.name[lang] }}
                </router-link>    
            </h3>
            <div class="info">
                <div class="item">
                    <p class="info-title">{{ pageText.category[lang] }}</p>
                    <p class="info-value">{{ category.name[lang] }}</p>
                </div>
                <div class="item">
                    <p class="info-title">{{ pageText.count[lang] }}</p>
                    <div class="count-field">
                        <button @click="count--">-</button>
                        <input type="number" v-model="count" min="1" step="1">
                        <button @click="count++">+</button>
                    </div>
                </div>
            </div>
            <div class="card-footer">
                <div class="price">
                    <p class="value">{{ product.cost }} UAH</p>
                </div>
                <div class="actions">
                    <app-button icon="fas fa-trash" @click="removeProductFromCart"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import AppButton from './AppButton.vue';

export default {
    data() {
        return {
            count: 1,
            pageText: {
                category: {
                    ua: 'Категорія',
                    ru: 'Категория',
                    en: 'Category'
                },
                count: {
                    ua: 'Кількість',
                    ru: 'Кол-во',
                    en: 'Count'
                }
            }
        }
    },
    props: {
        product: Object,
        index: Number
    },
    components: {
        AppButton
    },
    watch: {
        count(newValue) {
            this.count = Math.round(this.count);
            if (newValue <= 1) this.count = 1;
            this.$emit('changeValue', {
                index: this.index,
                count: this.count
            });
        },
        product(newValue) {
            this.count = newValue.count;
        }
    },
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        category() {
            return this.$store.getters['getCategoryById'](this.product.category);
        }
    },
    methods: {
        removeProductFromCart() {
            this.$store.dispatch('removeProductFromCart', this.index);
        }
    },
    created() {
        this.count = this.product.count;
    }
}
</script>
