<template>
    <div class="cart-view" v-if="Object.keys(pageContent).length">
        <page-title :title="pageContent.pageTitle[lang]"/>
        <div class="wrapper">
            <div class="flex-row-12" v-if="products.length && orderStatus != 'done'">
                <div class="flex-col xs-12 md-7">
                    <div class="products-list">
                        <cart-product-card :product="product" :index="i" v-for="(product, i) in products" :key="i" @changeValue="changeValue"/>
                    </div>
                </div>
                <div class="flex-col xs-12 md-5">
                    <div class="cart-form">
                        <h4 class="title">{{ pageContent.form.title[lang] }}</h4>
                        <div class="c-form">
                            <div class="field-item">
                                <label for="">{{ pageContent.form.fields.name[lang] }}</label>
                                <input :class="{'err': (isFormEmpty && !order.name.length)}" type="text" v-model="order.name" :placeholder="pageContent.form.fields.name[lang]">
                                <p v-if="isFormEmpty && !order.name.length" class="err-msg">{{ pageText.formFieldErrMsg[lang] }}</p>
                            </div>
                            <div class="field-item">
                                <label for="">{{ pageContent.form.fields.phone[lang] }}</label>
                                <input :class="{'err': (isFormEmpty && !order.phone.length)}" type="text" v-model="order.phone" :placeholder="pageContent.form.fields.phone[lang]">
                                <p v-if="isFormEmpty && !order.phone.length" class="err-msg">{{ pageText.formFieldErrMsg[lang] }}</p>
                            </div>
                            <div class="field-item">
                                <label for="">{{ pageContent.form.fields.email[lang] }}</label>
                                <input :class="{'err': (isFormEmpty && !order.email.length)}" type="text" v-model="order.email" :placeholder="pageContent.form.fields.email[lang]">
                                <p v-if="isFormEmpty && !order.email.length" class="err-msg">{{ pageText.formFieldErrMsg[lang] }}</p>
                            </div>
                            <div class="field-item">
                                <label for="">{{ pageContent.form.fields.adress[lang] }}</label>
                                <input :class="{'err': (isFormEmpty && !order.adress.length)}" type="text" v-model="order.adress" :placeholder="pageContent.form.fields.adress[lang]">
                                <p v-if="isFormEmpty && !order.adress.length" class="err-msg">{{ pageText.formFieldErrMsg[lang] }}</p>
                            </div>
                        </div>
                        <div class="total-price">
                            <p class="text">{{ pageContent.form.totalPriceText[lang] }}:</p>
                            <p class="value">{{ totalPrice }} UAH</p>
                        </div>
                        <div class="actions">
                            <app-button class="block" icon="fas fa-rocket" :isLoading="orderStatus == 'process'" :text="pageContent.form.btnText[lang]" @click="createOrder()"></app-button>
                        </div>
                    </div>
                </div>
            </div>
            <div class="msg" v-if="!products.length && orderStatus != 'done'">
                <span class="fas fa-shopping-cart"></span>
                <p>{{ pageText.emptyCart[lang] }}</p>
                <router-link class="btn" :to="{ name: 'categories'}">
                    <span class="btn-icon fas fa-list"></span>
                    <span class="btn-text">{{ pageText.catalogMoreBtn[lang] }}</span>
                </router-link>   
            </div>
            <div class="msg" v-if="orderStatus == 'done'">
                <span class="fas fa-check-double"></span>
                <p>{{ pageText.doneOrder[lang] }}</p>
                <router-link class="btn" :to="{ name: 'categories'}">
                    <span class="btn-icon fas fa-list"></span>
                    <span class="btn-text">{{ pageText.catalogMoreBtn[lang] }}</span>
                </router-link>   
            </div>
            <div class="msg" v-if="orderStatus == 'error'">
                <span class="fas fa-exclamation-triangle"></span>
                <p>{{ pageText.errorOrder[lang] }}</p>
                <router-link class="btn" :to="{ name: 'categories'}">
                    <span class="btn-icon fas fa-list"></span>
                    <span class="btn-text">{{ pageText.catalogMoreBtn[lang] }}</span>
                </router-link>   
            </div>
        </div>
        <questions-form />
    </div>
</template>

<script>
import AppButton from '../components/AppButton.vue';
import QuestionsForm from '../components/QuestionsForm.vue';
import PageTitle from '../components/PageTitle.vue';
import CartProductCard from '../components/CartProductCard.vue';

const date = new Date();

function getCurrentTextDate(separator) {
    return ((date.getHours() < 10) ? '0' + date.getHours() : date.getHours()) +
        ':' + 
        ((date.getMinutes() < 10) ? '0' + date.getMinutes() : date.getMinutes()) +
        separator + 
        ((date.getDate() < 10) ? '0' + date.getDate() : date.getDate()) + 
        '.' + 
        (((date.getMonth() + 1) < 10) ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1)) + 
        '.' + 
        date.getFullYear();
}

export default {
    data() {
        return {
            orderStatus: 'none',
            isFormEmpty: false,
            totalPrice: 0,
            order: {
                name: '',
                phone: '',
                email: '',
                adress: ''
            },
            products: [],
            pageText: {
                emptyCart: {
                    ua: 'Нажаль, кошик порожній',
                    ru: 'К сожалению, корзина пуста',
                    en: 'Sorry, but cart is empty'
                },
                doneOrder: {
                    ua: 'Замовлення успішно оформлено',
                    ru: 'Заказ был успешно оформлен',
                    en: 'Order has been successfully placed'
                },
                errorOrder: {
                    ua: 'Щось пішно не так. Спробуйте перезавантажити сторінку та замовити ще раз',
                    ru: 'Что-то пошло не так, попробуйте перезагрузить страницу и заказать снова',
                    en: 'Something went wrong, try reloading the page and ordering again'
                },
                catalogMoreBtn: {
                    ua: 'Перейти до каталогу',
                    ru: 'Перейти в каталог',
                    en: 'To catalog'
                },
                formFieldErrMsg: {
                    ua: 'Заповніть це поле',
                    ru: 'Заполните данное поле',
                    en: 'Fill this field'
                }
            }
        }
    },  
    components: {
        QuestionsForm,
        PageTitle,
        AppButton,
        CartProductCard
    },
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        pageContent() {
            return this.$store.getters['getCartPageContent'];
        },
        productsInCart() {
            return this.$store.getters['getProductsInCart'];
        }
    },
    watch: {
        productsInCart() {
            this.products = this.productsInCart;
        }
    },
    methods: {
        openFilterWindow() {
            this.isFilterWindowOpen = true;
        },
        closeFilterWindow() {
            this.isFilterWindowOpen = false;
        },
        changeValue(value) {
            this.products[value.index].count = value.count;
            this.updateTotalPrice();
            this.$store.dispatch('replaceProductsInCart', this.products);
            
        },
        createOrder() {
            this.isFormEmpty = true;
            if (this.order.name && this.order.phone && this.order.adress && this.order.email) {
                this.orderStatus = 'process';
                this.order.id = 'or' + Date.now();
                this.order.date = getCurrentTextDate(' ');
                this.order.docName = this.order.date + ' ' + this.order.id;
                this.order.products = this.products;
                this.order.totalPrice = this.totalPrice;
                this.order.status = {
					ready: false,
					send: false,
					done: false,
					payed: false
				}
				this.order.note = '';
                this.$store.dispatch('sendCartOrder', this.order)
                    .then(() => {
                        this.orderStatus = 'done';
                        this.$store.dispatch('replaceProductsInCart', []);
                        this.order = {
                            name: '',
                            phone: '',
                            email: '',
                            adress: ''
                        }
                        window.scrollTo({ top: 0, behavior: 'smooth' });
                    })
                    .catch(error => {
                        window.scrollTo({ top: 0, behavior: 'smooth' });
                        this.orderStatus = 'error';
                        console.error(error);
                    });
            }
        },
        updateTotalPrice() {
            this.totalPrice = 0;
            this.products.forEach(el => {
                this.totalPrice += parseFloat(el.cost) * parseFloat(el.count);
            });
        }
    },
    created() {
        this.products = this.productsInCart;
        this.updateTotalPrice();
    }
}
</script>