<template>
    <component :is="type" :href="href" class="btn" @click="clickEvent" :class="{'icon-btn': !text, 'loading': isLoading}">
        <span :class="'btn-icon ' + icon"></span>
        <span v-if="!!text" class="btn-text">{{ text }}</span>
        <div class="loader line-scale-pulse-out">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
        </div>
    </component>
</template>

<script>
    export default {
        props: {
            icon: String,
            text: String,
            isLoading: Boolean,
            href: String
        },
        computed: {
            type() {
                return (!this.href) ? 'button' : 'a';
            }
        },
        methods: {
            clickEvent(e) {
                this.$emit('click', e);
            }
        }
    }
</script>
