<template>
    <div class="page-title">
        <div class="wrapper">
            <div class="title">{{ title }}</div>
            <ul class="breadcrumbs">
                <li><router-link to="/">{{ itemTexts.home[lang] }}</router-link></li>
                <li v-for="(item, i) in breadcrumbs" :key="i">
                    <span class="fas fa-chevron-right"></span>
                    <router-link :to="getHref(i)">{{ getName(item, i) }}</router-link>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                itemTexts: {
                    home: {
                        ua: 'Головна',
                        ru: 'Главная',
                        en: 'Home'
                    },
                    about: {
                        ua: 'О нас',
                        ru: 'О нас',
                        en: 'About'
                    },
                    faq: {
                        ua: 'Питання та відповіді',
                        ru: 'Вопросы и ответы',
                        en: 'FAQ'
                    },
                    contacts: {
                        ua: 'Контакти',
                        ru: 'Контакты',
                        en: 'Contacts'
                    },
                    favorites: {
                        ua: 'Обране',
                        ru: 'Избранное',
                        en: 'Favorites'
                    },
                    cart: {
                        ua: 'Кошик',
                        ru: 'Корзина',
                        en: 'Cart'
                    },
                    categories: {
                        ua: 'Категоріі',
                        ru: 'Категории',
                        en: 'Categories'
                    }
                }
            }
        },
        props: {
            title: String
        },
        computed: {
            breadcrumbs() {
                return this.$route.fullPath.split('/').slice(1);
            },
            lang() {
                return this.$store.getters['getSelectedLang'];
            }
        },
        methods: {
            getHref(index) {
                let href = '/';
                let path = this.$route.fullPath.split('/').slice(1);
                for (let i = 0; i <= index; i++) {
                    if (i != 0) href += '/';
                    href += path[i];
                }
                return href;
            },
            getName(name, index) {
                if (index == 0) return this.itemTexts[name][this.lang];
                if (index == 1) return this.$store.getters['getCategoryById'](name).name[this.lang];
                if (index == 2) return this.$store.getters['getProductById'](name).name[this.lang];
            }
        }
    }
</script>
